* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.timer {
  max-width: 500px;
  background: rgba(35, 85, 90, .48);
  border: 2px solid rgba(0, 0, 255, .396);
  border-radius: 8px;
  gap: 20px;
  margin-top: 10px;
  padding: 5px;
  font-size: 25px;
  display: flex;
}

.field {
  text-align: center;
  background: rgba(139, 213, 219, .48);
  border: 2px solid rgba(0, 0, 255, .396);
  border-radius: 8px;
}

.value {
  color: rgba(230, 24, 17, .824);
  font-size: 30px;
}

.label {
  color: #006400;
}

